import * as React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"
import Me from "../components/me"
import CardItem from "../components/item"

const IndexPage = () => (
  <Layout>
    <Seo title="Home" />
    <Me />
    <Link to='/offer'>
      <CardItem title='Write-ups' description="Personal blogs on JavaScript, React, Design Systems and Life" >
        <StaticImage
          src="../images/write-ups.webp"
          width={500}
          quality={95}
          formats={["AUTO", "WEBP", "AVIF"]}
          alt="writeups Logo"
          className="m-12" />
      </CardItem>
    </Link>
    <Link to='/offer'>
      <CardItem title='Presentations' description="Presentations and Talks given to wider audience">
        <StaticImage
          src="../images/talks.webp"
          width={500}
          quality={95}
          formats={["AUTO", "WEBP", "AVIF"]}
          alt="talks Logo"
          className="m-12" />
      </CardItem>
    </Link>
  </Layout>
)

export default IndexPage
