import { Link } from "gatsby";
import * as React from "react"
import { FaFileDownload } from "react-icons/fa";
import Social from "./social";
import { StaticImage } from "gatsby-plugin-image"
import { useStaticQuery, graphql } from "gatsby"
import Resume from '../download/Resume-Charanbabu_Karnam.pdf'

const Me = () => {
    const { site } = useStaticQuery(
        graphql`
          query {
            site {
              siteMetadata {
                title
                designation
              }
            }
          }
        `
    )
    const designation = site.siteMetadata?.designation
    const defaultTitle = site.siteMetadata?.title
    const openResume = () => {
        console.log('clicked');
    }
    return (
        <div className="flex flex-col items-center justify-between">
            <div className="rounded-full w-40 h-40" >
                <StaticImage
                    src="../images/profile.png"
                    quality={95}
                    formats={["AUTO", "WEBP", "AVIF"]}
                    alt="KCB Logo"
                    className="mx-3"
                />
            </div>
            <h4 className="mt-2">{defaultTitle}</h4>
            <h6 className="text-center">{designation}</h6>
            <div className="flex justify-center items-center my-2">
                <a href={Resume} download className=" text-gray-600 hover:text-gray-200 font-semibold py-2 px-4 flex items-center">
                    Resume <FaFileDownload className="ml-1" onClick={openResume} />
                </a>

                <Link to='/offer'>
                    <button className=" mx-2 text-gray-600 hover:text-gray-200 font-semibold py-2 px-4">
                        My Offer
                    </button>
                </Link>
            </div>
            <Social />
        </div>
    )
}

export default Me
